/* src/css/UserList.css */
.user-list-container {
    padding: 20px;
    text-align: center;
    background-color: #1c1c1c;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    margin-bottom: 20px;
}

.user-list-container h1 {
    color: #ffffff;
    font-size: 50px;
    margin-bottom: 20px;
}

.user-list {
    list-style-type: none;
    padding: 0;
}

.user-list li {
    font-size: 18px;
    color: #34495e;
    padding: 10px 0;
    border-bottom: 1px solid #cde7ee;
    border-radius: 10px;
    background-color: #3498db;
    margin-bottom: 10px;
}

.user-list li:hover {
    color: #ffffff;
    cursor: pointer;
}
