/* src/css/VideoGallery.css */
.video-gallery-container {
    padding: 20px;
    text-align: center;
    background-color: #1c1c1c;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.video-gallery-container h2 {
    color: #8e44ad;
    font-size: 24px;
    margin-bottom: 15px;
}

.search-bar {
    margin-bottom: 20px;
}

.search-bar input[type="text"] {
    width: 200px;
    padding: 8px;
    border: 1px solid #bdc3c7;
    border-radius: 5px;
}

.search-bar button {
    padding: 8px 15px;
    margin-left: 10px;
    background-color: #8e44ad;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.search-bar button:hover {
    background-color: #9b59b6;
}

.video-gallery {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
}

.video-item {
    width: 320px;
    text-align: center;
}

.video-item video {
    width: 100%;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.video-item p {
    margin-top: 10px;
    font-size: 14px;
    color: #2c3e50;
}
