/* src/css/ImageGallery.css */

.image-gallery-container {
    padding: 20px;
    text-align: center;
    background-color: #1c1c1c; /* Fondo negro */
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    margin-bottom: 20px;
}

.image-gallery-container h2 {
    color: #f0f0f0; /* Color de texto claro */
    font-size: 50px;
    margin-bottom: 15px;
}

.image-gallery {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
}

.image-item {
    width: 150px;
    text-align: center;
}

.image-item img {
    width: 150px;
    height: 150px;
    object-fit: cover;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(255, 255, 255, 0.2); /* Sombra ligera blanca */
    border: 2px solid #333; /* Borde gris oscuro */
}

.image-item p {
    margin-top: 8px;
    font-size: 14px;
    color: #b3b3b3; /* Texto gris claro */
}

