/* src/css/App.css */
body {
  font-family: Arial, sans-serif;
  background-color: #253245;
  margin: 0;
  padding: 0;
}

.App {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h1, h2 {
  text-align: center;
  color: #34495e;
}

button {
  font-size: 16px;
}

button:hover {
  opacity: 0.9;
}

